import { EventsTypes } from 'redux/types'
import axios from 'axios';

const apiUrl = 'http://localhost:3001';

export const getEvents = (events) => {
    return {
        type: EventsTypes.GET_EVENTS,
        events
    };
};

export const getAllEvents = () => {
    return (dispatch) => {
        return axios.get(`${apiUrl}/events`)
            .then(response => {
                dispatch(getEvents(response.data))
            })
            .catch(error => {
                throw (error);
            });
    };
};

function searchEvent() {
    return { type: EventsTypes.SEARCH_EVENT }
}