import React from 'react'
import { NewEventsItem, NewEventsItemImage, NewEventsItemName, NewEventsButton, PlusIcon } from './NewEvents.style'
import { Row, Column } from 'styles'
import { history } from 'helpers'
import { Link } from 'react-router-dom'

class NewEvents extends React.Component {

    handleMoreEvent = () => {
        history.push('/eventos');
    }

    render() {
        const { events } = this.props;
        console.log('events: ', events);

        return (
            <React.Fragment>
                {events.length ?
                    (<React.Fragment><Row bottom="10px">
                        <Column flex={0.5}>
                            <Link to={`evento/${events[0].id}`}>
                                <NewEventsItem>
                                    <NewEventsItemImage imageSrc={`images/${events[0].image}`} alt="" />
                                    <NewEventsItemName>{events[0].name}</NewEventsItemName>
                                </NewEventsItem>
                            </Link>
                        </Column>
                        <Column flex={0.5}>
                            <Link to={`evento/${events[1].id}`}>
                                <NewEventsItem>
                                    <NewEventsItemImage imageSrc={`images/${events[1].image}`} alt="" />
                                    <NewEventsItemName>{events[1].name}</NewEventsItemName>
                                </NewEventsItem>
                            </Link>
                        </Column>
                    </Row>
                        <Row>
                            <Column>
                                <Link to={`evento/${events[2].id}`}>
                                    <NewEventsItem>
                                        <NewEventsItemImage imageSrc={`images/${events[2].image}`} alt="" />
                                        <NewEventsItemName>{events[2].name}</NewEventsItemName>
                                    </NewEventsItem>
                                </Link>
                            </Column>
                            <Column>
                                <NewEventsButton onClick={this.handleMoreEvent} aria-label="Botão para mostrar de eventos" ><PlusIcon size={50} /></NewEventsButton>
                            </Column>
                        </Row>
                    </React.Fragment>) : null}
            </React.Fragment>
        );
    }
}

export { NewEvents }
