import React from 'react'
import { EventItemWrapper, EventItemImage, EventItemTextWrapper } from './EventListItem.style'
import { Row, Column, Text, colors } from 'styles'
import { history } from 'helpers'
import moment from 'moment'

class EventListItem extends React.Component {

    openEventItem = id => {
        history.push(`evento/${id}`)
    }

    render() {
        const { event } = this.props;
        console.log('event: ', event);

        return (
            <EventItemWrapper onClick={id => this.openEventItem(event.id)}>
                <Row>
                    <Column flex={0.3}>
                        <EventItemImage imageSrc={`images/${event.image ? event.image : 'event-image-default.png'}`} alt="" />
                    </Column>
                    <Column flex={0.7}>
                        <EventItemTextWrapper>
                            <Text color={colors.red}>{moment(event.date).format("DD/MM/YYYY")}  - {event.hour}</Text>
                            <Text size={13} bold={600}>{event.name}</Text>
                            <Text color={colors.lightGray} size={14}>{event.address.city}/{event.address.uf}</Text>
                        </EventItemTextWrapper>
                    </Column>
                </Row>
            </EventItemWrapper>
        );
    }
}

export { EventListItem }
