import React from 'react'
import { connect } from 'react-redux';
import { Logo, PageHeader, Row, Column } from 'styles'
import { SearchButton, SearchIcon } from './Header.style'
import { getAllEvents } from 'redux/actions/events.actions';

class Header extends React.Component {

    componentDidMount() {
        this.props.getAllEvents();
    }

    onSearchEvents = () => {

    }

    render() {
        const { events } = this.props;

        return (
            <PageHeader>
                <Row>
                    <Column flex={0.8}>
                        <Logo src="images/logoMarca.png" alt="" />
                    </Column>
                    <Column flex={0.2} style={{ textAlign: 'right' }}>
                        <SearchButton onClick={this.onSearchEvents} aria-label="Botão para busca de eventos">
                            <SearchIcon size="20" />
                        </SearchButton>
                    </Column>
                </Row>
            </PageHeader>
        );
    }
}

const mapStateToProps = state => {
    return {
        events: state.events
    };
};

const ConnectedHeader = connect(
    mapStateToProps,
    { getAllEvents }
)(Header);

export { ConnectedHeader as Header }

